.MovieItem {
  width: 320px;
  border-radius: 2px;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}
.MovieListItem {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.MovieListTitle {
  color: #446179;
  padding-top: 15px;
  font-size: 22px;
  font-family: "Zen Tokyo Zoo", cursive;
  text-align: center;
}
.MovieListImg {
  width: 100%;
  /* height: 220px; */
  object-fit: cover;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.MovieListImg:hover {
  transform: scale(1.03);
  cursor: zoom-in;
}
